#header {
  padding: 24px 0;
}

.header-top {
  margin-bottom: 24px;
  background: white;
  border-radius: 0 0 28px 0;
  padding: 24px;

  @include media('<widescreen') {
    padding: 15px;
  }

  .desktop-logo {
    @include media('<widescreen') {
      display: none;
    }
  }
  .mobile-logo {
    display: none;
    @include media('<widescreen') {
      display: block;
    }
  }

  .label {
    background: #f8f9fa;
    border-radius: 24px;
    text-align: center;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 1.33;
    padding: 8px 10px;
    font-weight: bold;
    text-align: center;
    color: rgba($black, 0.48);
    display: block;
    margin-top: 12px;

    @include media('<widescreen') {
      display: none;
    }
  }
}

.header-nav {
  background: white;
  border-radius: 0 28px 28px 0;
  margin-bottom: 24px;
  padding: 40px 0 40px 24px;

  @include media('<widescreen') {
    padding: 30px 15px;
  }

  ul {
    @extend %listreset;
    font-family: $secondary-font;
    font-size: 14px;
    font-weight: 500;
    li + li {
      margin-top: 24px;
    }
    li:hover,
    li.active {
      a {
        border-right: 2px solid $light-blue;
        background-image: linear-gradient(to right, #ffffff 0%,#f0faff 100%);
        color: $light-blue;

        @include media('<widescreen') {
          border-right: 0;
          background-image: none;
        }

        i {
          color: $light-blue;
        }
      }
    }
    li.active {
      font-weight: bold;
    }
    a {
      display: flex;
      align-items: center;
      border-right: 2px solid transparent;

      i {
        background: $icon-bg;
        flex-shrink: 0;
        border-radius: 12px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        margin-right: 16px;
        line-height: 1;
        overflow: hidden;
        color: #9da1a3;
        font-size: 20px;

        @include media('<widescreen') {
          margin-right: 0;
        }
      }
      span {
        @include media('<widescreen') {
          display: none;
        }
      }
    }
  }
}

.header-bottom {
  margin-top: auto;
  padding: 24px;
  background: white;
  border-radius: 0 28px 0 0;

  @include media('<widescreen') {
    padding: 15px;
  }
}

.logout {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: $black;
  font-family: $secondary-font;
  font-weight: 500;
  
  i {
    background: #faf0f0;
    border-radius: 12px;
    display: inline-flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    margin-right: 16px;
    color: #fa6969;
    font-size: 20px;
  }
  span {
    @include media('<widescreen') {
      display: none;
    }
  }
}