@font-face {
  font-family: 'icomoon';
  src:  
    url('../fonts/icomoon.ttf?w3j3u3') format('truetype'),
    url('../fonts/icomoon.woff?w3j3u3') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.ui-icon, [class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

