//  create new user

.new-user {
  max-width: 900px;
  .holder {
    display: flex;
    @include media('<desktop') {
      flex-direction: column;
    }
  }
  .column {
    width: 50%;
    padding: 0 40px 20px 0;
    @include media('<desktop') {
      width: 100%;
      padding: 0;
    }
  }
  .column + .column {
    padding-left: 40px;
    border-left: 1px solid #f8f9fa;
    @include media('<desktop') {
      padding-left: 0;
      border: none;
    }
  }
  .form-control-group {
    display: flex;
    .form-control + .form-control {
      margin-left: 8px;
    }
    .radio + .radio {
      margin-left: 35px;
    }
  }
  input[type='text'],
  input[type='tel'],
  input[type='email'],
  input[type='search'],
  input[type='password'] {
    width: 100%;
  }
  .location {
    .form-control {
      flex-basis: 1px;
      flex-grow: 1;
    }
    .city {
      flex-grow: 2;
    }
  }
  .email {
    flex-grow: 2;
  }
  .button-row {
    border-top: 1px solid #f8f9fa;
    display: flex;
    padding-top: 35px;
    .button + .button {
      margin-left: 15px;
    }
  }
}

// btnAdd
.btnRemove,
.btnAdd {
  background: #f0f7f1;
  width: 32px;
  height: 32px;
  position: relative;
  min-width: 1px;
  padding: 0;
  border-radius: 8px;
  margin-top: 42px;
  &:after {
    content:'';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    border: 1px solid #6acc74;
    border-radius: 50%;
  }
  &:before {
    content:'+';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #6acc74;
    font-size: 14px;
    line-height: 1;
  }
}
.btnRemove {
  &:after {
    border-color: #fa6969;
  }
  &:before {
    content:'-';
    color: #fa6969;
    font-size: 20px;
    margin-top: -1px;
  }
}