// login form
.login-form {
  width: 100%;
  max-width: 392px;

  input {
    display: block;
    width: 100%;
    border: none;
  }
  h1 {
    font-size: 24px;
    margin: 0 0 30px;
  }
  button {
    margin-top: 8px;
  }
  .toggle-password {
    position: absolute;
    right: 24px;
    top: 17px;
    color: #9da1a3;
    opacity: 0.65;
    cursor: pointer;
    &:hover {
      opacity: 1;
    }
    &.active {
      color: #6acc74;
      &:before {
        content: "\e904";
      }
    }
  }
}