// forms

form,
fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
}

input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
//input[type='url'],
// input[type='date'],
textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  box-sizing: border-box;
  border: none;
  padding: $form-element-padding;
  background: white;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 500;
  font-family: $secondary-font;
  border: 1px solid #e0e0e0;

  // &:hover {
    
  // }

  &:focus {
    border-color: $form-element-focus-border-color;
    outline: none;
  }
  &:disabled {
    opacity: 0.4;
  }

  @include placeholder {
    color: $placeholder-color;
  }
}

select {
  -webkit-border-radius: 0;
  width: 100%;
  &:focus {
    outline: none;
  }
}
.select2 {
  width: 100% !important;
}

textarea {
  resize: vertical;
  vertical-align: top;
}

button,
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  cursor: pointer;
}

label {
  font: 15px/1.6 "Montserrat", "Arial", "Helvetica Neue", "Helvetica", sans-serif;
  font-weight: normal;
  color: #4d4d4d;
  font-weight: bold;
  display: block;
  padding-bottom: 8px;
}

.form-control {
  padding-bottom: 16px;
  position: relative;
}

// radio
.radio {
  display: flex;
  align-items: center;
  padding: 0;

  input[type='radio'] {
    display: none;
  }
  .fake-input {
    display: block;
    flex-shrink: 0;
    background-color: #f8f9fa;
    border-radius: 12px;
    width: 48px;
    height: 48px;
    position: relative;
    margin-right: 10px;
    &:before {
      content:'';
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 24px;
      height: 24px;
      border-radius: 50%;
      border: 1px solid #e0e0e0;
      background-color: #fff;
    }
  }
  .fake-label {
    opacity: .49;
  }
  input[type='radio']:checked + .fake-input {
    &:before {
      border: 1px solid $light-blue;
      background-color: $light-blue;
    }
    &:after {
      content:'';
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #fff;
    }
  }
  input[type='radio']:checked ~ .fake-label {
    opacity: 1;
  }
}

button, .button {
  background: $btn-background;
  border-radius: 12px;
  color: $btn-color;
  border: none;
  padding: $btn-padding;
  font-weight: bold;
  font-size: $btn-font-size;
  line-height: 1.3;
  min-width: 138px;
  text-align: center;
  cursor: pointer;
  @extend %transition;

  &:disabled {
    opacity: .4;
  }

  &.cancel {
    background-color: #f0f7fa;
    color: #4d4d4d;
  }

}

.btn-secondary {
  background: white;
  border-radius: 12px;
  color: $light-blue;
  border: 1px solid $light-blue;
  padding: $btn-padding;
  font-weight: bold;
  font-size: $btn-font-size;
  line-height: 1.3;
  min-width: 138px;
  cursor: pointer;
  display: inline-block;
  text-align: center;
}

.date {
  border: 1px solid #e0e0e0;
  width: 180px;
  height: 48px;
  background: white;
  border-radius: 12px;
  &.ui-active {
    i {
      color: white;
      background: #2a8ab8;
    }
  }
  
  position: relative;
  overflow: hidden;
  input {
    background: none;
    border: none;
    padding: $form-element-padding;
    padding-left: 64px;
    font-size: 16px;
    font-weight: 500;
    font-family: $secondary-font;
    display: block;
    width: 100%;
  }
  i {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    background: #f2f8fa;
    color: #9da1a3;
    width: 48px;
    font-size: 20px;
    border-radius: 12px 0 0 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
  }
  .ico-text {
    font: 600 18px/1.33 $secondary-font;
    color: rgba($black, 0.48);
  }
}

// date picker
.ui-datepicker {
  margin-top: 24px;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  box-shadow: 0 8px 12px #e0ecf2;
  width: auto;
  &:before {
    position: absolute;
    left: 14px;
    top: -12px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 12px 12px 12px;
    display: block;
    border-color: transparent transparent #e0e0e0 transparent;
  }
  &:after {
    position: absolute;
    left: 14px;
    top: -10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 12px 12px 12px;
    display: block;
    border-color: transparent transparent #fff transparent;
  }

  a.ui-state-default {
    border: none;
    width: 40px;
    height: 40px;
    text-align: center;
    font: 600 15px/40px $secondary-font;
    color: $light-blue;
    padding: 0;
    background: none;
    border-radius: 12px;
    &.ui-state-hover,
    &.ui-state-active {
      color: white;
      background: $light-blue;
    }
  }
  .ui-datepicker-header {
    background: none;
    border: none;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before,
    &:after {
      display: none;
    }
  }
  .ui-datepicker-title {
    font: 600 15px/24px $secondary-font;
    color: #4d4d4d;
  }
  th {
    opacity: .48;
    font: 600 12px/16px $secondary-font;
    text-transform: uppercase;
    border-bottom: 1px solid #f3f3f3;
  }
  .ui-datepicker-prev, .ui-datepicker-next {
    width: 24px;
    height: 24px;
    border-radius: 8px;
    background: #edf4f7;
    position: relative;
    top: 0;
    left: 0;
    border: none;
    cursor: pointer;
    
    .ui-icon {
      margin: 0;
      background: none;
      transform: translate(-50%, -50%);
      color: $light-blue;
    }
  }
  .ui-datepicker-prev {
    .ui-icon:before {
      content: '\e911';
      display: flex;
      text-indent: 0;
      font-size: 22px;
      line-height: 1;
      margin: -4px 0 0 -4px;
    }
  }
  .ui-datepicker-next {
    order: 3;
    .ui-icon:before {
      content: '\e910';
      display: flex;
      text-indent: 0;
      font-size: 22px;
      line-height: 1;
      margin: -4px 0 0 -2px;
    }
  }
}

// select2
.select2-container {
  .select2-selection--single {
    height: 48px;
    background: #f2f8fa;
    border: 1px solid #e0e0e0;
    border-radius: 12px;
  }

  &.select2-container--open {
    .select2-selection--single {
      background: #2a8ab8;
      .select2-selection__placeholder {
        color: white;
      }
      .select2-selection__arrow b {
        color: white;
      }
      .select2-selection__rendered {
        color: white;
      }
    }
  }
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  line-height: 48px;
  padding-left: 24px;
  padding-right: 48px;
  font-size: 16px;
  font-weight: 500;
  font-family: $secondary-font;
}
.select2-container--default .select2-selection--single .select2-selection__placeholder {
  font-size: 16px;
  font-weight: 500;
  font-family: $secondary-font;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  height: 48px;
  width: 48px;
}
.select2-container--default .select2-selection--single .select2-selection__arrow b {
  @extend %icon-font;
  border: none;
  height: auto;
  width: auto;
  margin-top: -12px;
  margin-left: -12px;
  color: #979797;
  &:before {
    content: '\e912';
    font-size: 24px;
    line-height: 1;
  }
}
span.select2-selection.select2-selection--single {
  outline: none;
}

.select2-dropdown {
  box-shadow: 0 8px 12px #e0ecf2;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  padding-top: 8px;
  padding-bottom: 8px;
}
.select2-container--default .select2-results__group {
  font-size: 12px;
  line-height: 16px;
  font-weight: 600;
  font-family: $secondary-font;
  text-transform: uppercase;
  opacity: 0.48;
  display: flex;
  padding-left: 24px;
  padding-right: 24px;
  .r {
    margin-left: auto;
  }
}
.select2-container--default .select2-results__option[role="option"] {
  display: flex;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 8px;
  padding-bottom: 8px;

  &.select2-results__option--highlighted {
    background: #f2f8fa;
    color: $black;
  }
  &:hover {
    background: #2a8ab8;
    color: white;
  }

  .r {
    margin-left: auto;
    font-weight: 700;
  }
}

.select2-container--default .select2-results__option[aria-selected=true] {
  background: #f2f8fa;
  color: $black;
}

.js-active-form-errors {
  padding-bottom: 20px;
  color: red;
}