// icon buttons
.btn-icon {
  width: 32px;
  min-width: 32px;
  height: 32px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  padding: 0;
  font-size: 14px;
  line-height: 1;
  overflow: hidden;
}
.btn-default {
  color: #9da1a3;
  background: #f2f8fa;
}
.btn-primary {
  color: $light-blue;
  background: #f2f8fa;
}
.btn-danger {
  color: #fa6969;
  background: #faf2f2;
}
.btn-success {
  color: #9da1a3;
  background: #f2f8fa;
  &.active,
  &:hover {
    background: #f0faf1;
    color: #6acc74;
  }
}

.btn-view {
  &.active,
  &:hover {
    i:before {
      content: "\e904";
    }
  }
}