// sort bar
.sort-bar {
  margin-right: -32px;
  background: #f8f9fa;
  border-radius: 20px 0 0 20px;
  height: 40px;
  display: flex;
  align-items: center;
  padding: 0 24px;
  margin-bottom: -37px;

  .sort-label {
    font: 700 16px/1.5 $secondary-font;
    color: rgba($black, 0.48);
    cursor: pointer;
    i {
      font-size: 14px;
      margin-left: 4px;
    }
  }
}

// yearly-report
.yearly-report {
  &-row {
    overflow: hidden;
    @include media('>960px') {
      display: flex;
      min-width: 1px;
    }
    & + & {
      border-top: 1px solid #f8f9fa;
    }
  }
  &-info {
    width: 338px;
    padding: 0 24px 24px;
    margin-top: 48px;
    font: 12px/1.33 $secondary-font;
    flex-shrink: 0;
    @include media('<desktop') {
      width: 270px;
    }
    .name {
      display: block;
      font-size: 16px;
      line-height: 1.5;
      margin-bottom: 14px;
    }
    dl {
      margin: 0;
      dt {
        float: left;
        clear: both;
        font-weight: 700;
      }
      dd {
        overflow: hidden;
        text-align: right;
        margin-bottom: 9px;
      }
    }
  }
  &-table {
    min-width: 1px;
    position: relative;
    border-left: 1px solid #f8f9fa;
    .scroller {
      overflow-x: auto;
      margin-left: 160px;
    }

    table {
      font: 16px/1.5 $secondary-font;
      th,td {
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 20px;
        padding-right: 4px;
        white-space: nowrap;
        min-width: 110px;
      }
      th {
        width: 160px;
        position: absolute;
        left: 0;
        top: auto;
      }
      thead {
        th {
          opacity: 0.48;
          text-transform: uppercase;
          font-size: 12px;
          line-height: 20px;
          text-align: left;
        }

        td {
          text-align: right;
          font-size: 16px;
          line-height: 20px;
          font-weight: 700;
          .num {
            font-size: 12px;
            opacity: .48;
          }
        }
      }

      tbody {
        th {
          font-weight: 700;
          text-align: left;
          padding-left: 20px;
          padding-right: 10px;
        }
        td {
          text-align: right;
        }
      }
    }
  }
}

