// Colors
// Color calculator - http://razorjam.github.io/sasscolourfunctioncalculator/
// Color names - http://name-of-color.com/

$gray: #999;
$black: #212121;
$white: #fff;
$light-blue: #3abfff;
$bg-color: #edf2f7;
$icon-bg: #f2f8fa;

$base-font-sans-serif: 'Nunito Sans', 'Arial', 'Helvetica Neue', 'Helvetica', sans-serif;
$secondary-font: 'Montserrat', 'Arial', 'Helvetica Neue', 'Helvetica', sans-serif;
$base-font-serif: 'Times New Roman', 'Times', 'Baskerville', 'Georgia', serif;

// Body
$base-text-color: $black !default;
$base-background-color: $bg-color !default;
$base-font-size: 16px !default;
$base-line-height: 1.2 !default;
$base-font-family: $base-font-sans-serif !default;

$vertical-rhythm: ($base-line-height * 1em);

$base-min-width: 320px;

// Liks
$base-link-color: $light-blue;

// Buttons 
$btn-color: $white !default;
$btn-background: $light-blue !default;
$btn-padding: 10px 24px !default;
$btn-font-size: 16px !default;

// Forms
$form-element-padding: 14px 24px;
$form-element-border-color: $gray !default;
$form-element-focus-border-color: $black !default;
$placeholder-color: $gray !default;

// Headers
$h1-font-size: 30px !default;
$h2-font-size: 27px !default;
$h3-font-size: 24px !default;
$h4-font-size: 21px !default;
$h5-font-size: 17px !default;
$h6-font-size: 15px !default;

$headings-font-family: $secondary-font !default;
$headings-color: inherit !default;


$animation-speed: 0.3s;
