//table
.main-table {
  flex-grow: 1;

  .table-frame {
    width: 100%;
    overflow: auto;
  }

  table {
    width: 100%;
    min-width: 834px;

    thead {
      background: #f8f9fa;
      tr {
        border-radius: 20px;
      }
      td {
        background: #f8f9fa;
        font-family: $secondary-font;
        font-weight: 700;
        opacity: 0.48;
        &:first-child {
          border-radius: 20px 0 0 20px;
        }
        &:last-child {
          border-radius: 0 20px 20px 0;
        }
      }
    }

    tbody {
      tr {
        border-bottom: 1px solid #f3f3f3;
        &:hover {
          td {
            background: rgba($light-blue, 0.05);
          }
        }
      }
    }

    td {
      padding: 14px 18px;
      font-size: 16px;
      @include media('<widescreen') {
        font-size: 14px;
        padding-left: 14px;
        padding-right: 14px;
      }
      &:last-child {
        min-width: 180px;
        text-align: right;
      }
    }
    button + button{
      margin-left: 10px;
      @include media('<widescreen') {
        margin-left: 5px;
      }
    }
  }
}