// monthly report form
.monthly-report {
  .form-inline {
    
    @include media('>1280px') {
      display: flex;
    }
    align-items: flex-start;
    & + .form-inline {
      border-top: 1px solid #f3f3f3;
      padding-top: 24px;
      margin-top: 16px;
    }
  }
  .form-control {
    margin-right: 24px;
  }
  .form-control-group {
    display: flex;
    margin-right: 24px;
    .form-control { 
      margin-right: 8px;
    }
  }

  select {
    width: 360px;
  }
  button {
    height: 48px;
    margin-top: 30px;
  }

  label {
    font: 15px/1.6 $secondary-font;
    color: #4d4d4d;
    font-weight: bold;
    display: block;
    padding-bottom: 8px;
  }

  .btn-secondary {
    width: 360px;
  }
}