// sort label
.sort-label {
  vertical-align: middle;
  cursor: pointer;
  white-space: nowrap;
  i {
    font-size: 13px;
    margin-left: 4px;
  }
  &.sort-abc {
    i {
      color: $light-blue;
      &:before {
        content: "\e90d";
      }
    }
  }
  &.sort-cba {
    i {
      color: $light-blue;
      &:before {
        content: "\e90c";
      }
    }
  }
}