.radio {
	display: flex;
	align-items: center;
	padding: 0;
}

.radio input[type="radio"] {
	display: none;
}

.radio .fake-input {
	display: block;
	flex-shrink: 0;
	background-color: #f8f9fa;
	border-radius: 12px;
	width: 48px;
	height: 48px;
	position: relative;
	margin-right: 10px;
}

.radio .fake-input:before {
	content: "";
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 24px;
	height: 24px;
	border-radius: 50%;
	border: 1px solid #e0e0e0;
	background-color: #fff;
}

.radio .fake-label {
	opacity: .49;
}

.radio input[type="radio"]:checked
+ .fake-input:before {
	border: 1px solid #3abfff;
	background-color: #3abfff;
}

.radio input[type="radio"]:checked
+ .fake-input:after {
	content: "";
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background-color: #fff;
}

.radio input[type="radio"]:checked
~ .fake-label {
	opacity: 1;
}

