	border-radius: 12px;
	box-shadow: 0 8px 12px #e0ecf2;
	width: auto;
}

.ui-datepicker:before {
	position: absolute;
	left: 14px;
	top: -12px;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 0 12px 12px 12px;
	display: block;
	border-color: transparent transparent #e0e0e0 transparent;
}

.ui-datepicker:after {
	position: absolute;
	left: 14px;
	top: -10px;
	width: 0;
	height: 0;
