}

.new-user .column
+ .column {
	padding-left: 40px;
	border-left: 1px solid #f8f9fa;
}

.new-user .form-control-group {
	display: flex;
}

.new-user .form-control-group .form-control
+ .form-control {
	margin-left: 8px;
}

.new-user .form-control-group .radio
+ .radio {
	margin-left: 35px;
}

.new-user input[type="text"],
.new-user input[type="tel"],
.new-user input[type="email"],
.new-user input[type="search"],
.new-user input[type="password"] {
	width: 100%;
}

.new-user .location .form-control {
	flex-basis: 1px;
	flex-grow: 1;
}

.new-user .location .city {
	flex-grow: 2;
}

.new-user .email {
	flex-grow: 2;
}

.new-user .button-row {
