	display: flex;
	padding-top: 35px;
}

.new-user .button-row .button
+ .button {
	margin-left: 15px;
}

.btnRemove,
.btnAdd {
	background: #f0f7f1;
	width: 32px;
	height: 32px;
	position: relative;
	min-width: 1px;
	padding: 0;
	border-radius: 8px;
	margin-top: 42px;
}

.btnRemove:after,
.btnAdd:after {
	content: "";
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 20px;
	height: 20px;
	border: 1px solid #6acc74;
	border-radius: 50%;
}

.btnRemove:before,
.btnAdd:before {
	content: "+";
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: #6acc74;
	font-size: 14px;
	line-height: 1;
}

.btnRemove:after {
	border-color: #fa6969;
}

.btnRemove:before {
	content: "-";
	color: #fa6969;
	font-size: 20px;
	margin-top: -1px;
}

.wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

main {
	flex-grow: 1;
	display: flex;
	min-width: 1px;
}

.container {
	padding: 0 24px;
}

.flex-center {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-grow: 1;
}

.wrapper.l-two-columns {
	flex-direction: row;
	min-width: 1px;
}

.wrapper.l-two-columns #header {
	width: 208px;
	display: flex;
	flex-direction: column;
	padding: 0;
	flex-shrink: 0;
}

.wrapper.l-two-columns main {
	display: flex;
	flex-direction: column;
	margin: 0 24px;
}

.main-container {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	margin-top: 24px;
	border-radius: 28px;
	background: white;
	padding: 32px;
}

.main-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-bottom: 40px;
}

.main-header h1 {
	margin: 0;
	font-size: 24px;
}

.main-header p {
	margin: 8px 0 0;
}

.main-header .controls button {
	margin-left: 10px;
	width: 40px;
	height: 40px;
	font-size: 20px;
}

@font-face {
	font-family: "icomoon";
	src: url("../fonts/icomoon.ttf?w3j3u3") format("truetype"), url("../fonts/icomoon.woff?w3j3u3") format("woff");
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

@media (min-width: 961px) {
	.yearly-report-row {
		display: flex;
		min-width: 1px;
	}
}

@media (min-width: 1281px) {
	.monthly-report .form-inline {
		display: flex;
	}
}

@media (max-width: 1199px) {
	.header-top {
		padding: 15px;
	}

	.header-top .desktop-logo {
		display: none;
	}

	.header-top .mobile-logo {
		display: block;
	}

	.header-top .label {
		display: none;
	}

	.header-nav {
		padding: 30px 15px;
	}

	.header-nav ul li:hover a,
	.header-nav ul li.active a {
		border-right: 0;
		background-image: none;
	}

	.header-nav ul a i {
		margin-right: 0;
	}

	.header-nav ul a span {
		display: none;
	}

	.header-bottom {
		padding: 15px;
	}

	.logout span {
		display: none;
	}

	.main-table table td {
		font-size: 14px;
		padding-left: 14px;
		padding-right: 14px;
	}

	.main-table table button
	+ button {
		margin-left: 5px;
	}

	.wrapper.l-two-columns #header {
		width: 78px;
	}
}

@media (max-width: 1023px) {
	.yearly-report-info {
		width: 270px;
	}

	.new-user .holder {
		flex-direction: column;
	}

	.new-user .column {
		width: 100%;
		padding: 0;
	}

	.new-user .column
	+ .column {
