// pagination
.pagination {
  align-items: center;
  justify-content: space-between;
  display: flex;
  padding-top: 24px;
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    font-size: 16px;
    line-height: 2;
    font-weight: bold;

    li {
      margin-right: 8px;
      min-width: 32px;
      text-align: center;
      border-radius: 8px;

      &.current-page {
        background: $light-blue;
        color: white;
      }

      &.prev,
      &.next {
        a {
          display: flex;
          color: $light-blue;
          font-size: 12px;
          align-items: center;
          width: 32px;
          height: 32px;
          justify-content: center;
          border-radius: 8px;
          &:hover {
            background: #edf4f7;
          }
        }
      }
    }
    a {
      color: $black;
      text-decoration: none;
      &:hover {
        color: $light-blue;
      }
    }
  }
  .details {
    opacity: 0.48;
    font-size: 16px;
    font-weight: 500;
  }
}