$breakpoints: (
  'xs-phone': 320px,
  'phone': 480px,
  'tablet': 768px,
  'desktop': 1024px,
  'widescreen': 1200px
);

// keywords
$media-expressions: (
  'screen': 'screen',
  'print': 'print',
  'handheld': 'handheld',
  'landscape': '(orientation: landscape)',
  'portrait': '(orientation: portrait)',
  'retina2x': '(-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi)',
  'retina3x': '(-webkit-min-device-pixel-ratio: 2.5), (min-resolution: 240dpi)'
);

// ligatured operators ≥ ≤

@import 'vendors/include-media';
@import 'vendors/normalize';
@import 'vendors/jquery-ui';
@import 'vendors/select2';

@import 'base/variables';
@import 'base/functions';
@import 'base/mixins';
@import 'base/helpers';
@import 'base/reset';
@import 'base/typography';
@import 'base/forms';
@import 'base/icons';

@import 'components/header';
@import 'components/footer';
@import 'components/pagination';
@import 'components/iconButtons';
@import 'components/sortLabel';

@import 'pages/login';
@import 'pages/runMonthlyReport';
@import 'pages/noteholders';
@import 'pages/yearlyReport';
@import 'pages/create-noteholder';


body{ 
  @include media('<desktop') {

  }
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex-grow: 1;
  display: flex;
  min-width: 1px;
}

.container {
  padding: 0 24px;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

// two column layout
.wrapper.l-two-columns {
  flex-direction: row;
  min-width: 1px;
  
  #header {
    width: 208px;
    display: flex;
    flex-direction: column;
    padding: 0;
    flex-shrink: 0;

    @include media('<widescreen') {
      width: 78px;
    }
  }

  main {
    display: flex;
    flex-direction: column;
    margin: 0 24px;
  }
}

.main-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin-top: 24px;
  border-radius: 28px;
  background: white;
  padding: 32px;
}

.main-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 40px;

  h1 {
    margin: 0;
    font-size: 24px;
  }
  p {
    margin: 8px 0 0;
  }

  .controls {
    button {
      margin-left: 10px;
      width: 40px;
      height: 40px;
      font-size: 20px;
    }
  }
}